module.exports = "\n<div>\n    <article>\n        <h1>Collapse 折叠面板</h1>\n        <anchor title=\"概述\" h2></anchor>\n        <p>将内容区域折叠/展开。</p>\n        <anchor title=\"代码示例\" h2></anchor>\n        <demo title=\"基础用法\">\n            <div slot=\"demo\">\n                <Collapse name=\"{=value1=}\">\n                    <Panel name=\"1\">\n                        <span slot=\"header\">史蒂夫·乔布斯</span>\n                        <p slot=\"content\">史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>\n                    </Panel>\n                    <Panel name=\"2\">\n                        <span slot=\"header\">斯蒂夫·盖瑞·沃兹尼亚克</span>\n                        <p slot=\"content\">斯蒂夫·盖瑞·沃兹尼亚克（Stephen Gary Wozniak），美国电脑工程师，曾与史蒂夫·乔布斯合伙创立苹果电脑（今之苹果公司）。斯蒂夫·盖瑞·沃兹尼亚克曾就读于美国科罗拉多大学，后转学入美国著名高等学府加州大学伯克利分校（UC Berkeley）并获得电机工程及计算机（EECS）本科学位（1987年）。</p>\n                    </Panel>\n                    <Panel name=\"3\">\n                        <span slot=\"header\">乔纳森·伊夫</span>\n                        <p slot=\"content\">乔纳森·伊夫是一位工业设计师，现任Apple公司设计师兼资深副总裁，英国爵士。他曾参与设计了iPod，iMac，iPhone，iPad等众多苹果产品。除了乔布斯，他是对苹果那些著名的产品最有影响力的人。</p>\n                    </Panel>\n                </Collapse>\n            </div>\n            <div slot=\"desc\">\n                <p>默认可以同时展开多个面板，可以设置默认展开第几个。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.base }}</i-code>\n        </demo>\n        <demo title=\"手风琴\">\n            <div slot=\"demo\">\n                <Collapse accordion name=\"{=value2=}\">\n                    <Panel name=\"1\">\n                        <span slot=\"header\">史蒂夫·乔布斯</span>\n                        <p slot=\"content\">史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>\n                    </Panel>\n                    <Panel name=\"2\">\n                        <span slot=\"header\">斯蒂夫·盖瑞·沃兹尼亚克</span>\n                        <p slot=\"content\">斯蒂夫·盖瑞·沃兹尼亚克（Stephen Gary Wozniak），美国电脑工程师，曾与史蒂夫·乔布斯合伙创立苹果电脑（今之苹果公司）。斯蒂夫·盖瑞·沃兹尼亚克曾就读于美国科罗拉多大学，后转学入美国著名高等学府加州大学伯克利分校（UC Berkeley）并获得电机工程及计算机（EECS）本科学位（1987年）。</p>\n                    </Panel>\n                    <Panel name=\"3\">\n                        <span slot=\"header\">乔纳森·伊夫</span>\n                        <p slot=\"content\">乔纳森·伊夫是一位工业设计师，现任Apple公司设计师兼资深副总裁，英国爵士。他曾参与设计了iPod，iMac，iPhone，iPad等众多苹果产品。除了乔布斯，他是对苹果那些著名的产品最有影响力的人。</p>\n                    </Panel>\n                </Collapse>\n            </div>\n            <div slot=\"desc\">\n                <p>通过设置属性<code>accordion</code>开启手风琴模式，每次只能打开一个面板。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.accordion }}</i-code>\n        </demo>\n        <demo title=\"面板嵌套\">\n            <div slot=\"demo\">\n                <Collapse accordion name=\"{=value3=}\">\n                    <Panel name=\"1\">\n                        <span slot=\"header\">史蒂夫·乔布斯</span>\n                        <div slot=\"content\">\n                            史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。\n                            <Collapse accordion name=\"{=value4=}\">\n                                <Panel name=\"1-1\">\n                                    <span slot=\"header\">iPhone</span>\n                                    <p slot=\"content\">iPhone，是美国苹果公司研发的智能手机，它搭载iOS操作系统。第一代iPhone于2007年1月9日由苹果公司前首席执行官史蒂夫·乔布斯发布，并在2007年6月29日正式发售。</p>\n                                </Panel>\n                                <Panel name=\"1-2\">\n                                    <span slot=\"header\">iPad</span>\n                                    <p slot=\"content\">iPad是由苹果公司于2010年开始发布的平板电脑系列，定位介于苹果的智能手机iPhone和笔记本电脑产品之间，（屏幕中有4个虚拟程序固定栏）与iPhone布局一样，提供浏览网站、收发电子邮件、观看电子书、播放音频或视频、玩游戏等功能。由英国出生的设计主管乔纳森·伊夫（Jonathan Ive）（有些翻译为 乔纳森·艾维）领导的团队设计的，这个圆滑、超薄的产品反映出了伊夫对德国天才设计师Dieter Rams的崇敬之情。</p>\n                                </Panel>\n                            </Collapse>\n                        </div>\n                    </Panel>\n                    <Panel name=\"2\">\n                        <span slot=\"header\">斯蒂夫·盖瑞·沃兹尼亚克</span>\n                        <p slot=\"content\">斯蒂夫·盖瑞·沃兹尼亚克（Stephen Gary Wozniak），美国电脑工程师，曾与史蒂夫·乔布斯合伙创立苹果电脑（今之苹果公司）。斯蒂夫·盖瑞·沃兹尼亚克曾就读于美国科罗拉多大学，后转学入美国著名高等学府加州大学伯克利分校（UC Berkeley）并获得电机工程及计算机（EECS）本科学位（1987年）。</p>\n                    </Panel>\n                    <Panel name=\"3\">\n                        <span slot=\"header\">乔纳森·伊夫</span>\n                        <p slot=\"content\">乔纳森·伊夫是一位工业设计师，现任Apple公司设计师兼资深副总裁，英国爵士。他曾参与设计了iPod，iMac，iPhone，iPad等众多苹果产品。除了乔布斯，他是对苹果那些著名的产品最有影响力的人。</p>\n                    </Panel>\n                </Collapse>\n            </div>\n            <div slot=\"desc\">\n                <p>折叠面板可以进行嵌套。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.nest }}</i-code>\n        </demo>\n        <div class=\"api\">\n            <anchor title=\"API\" h2></anchor>\n            <anchor title=\"Collapse props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>value</td>\n                        <td>当前激活的面板的 name，可以使用 v-model 双向绑定</td>\n                        <td>Array | String</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>accordion</td>\n                        <td>是否开启手风琴模式，开启后每次至多展开一个面板</td>\n                        <td>Boolean</td>\n                        <td>false</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Collapse events\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>事件名</th>\n                        <th>说明</th>\n                        <th>返回值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>on-select</td>\n                        <td>当前选中的面板name</td>\n                        <td></td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Panel props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>name</td>\n                        <td>当前面板的 name，与 Collapse的<code>value</code>对应，不填为索引值</td>\n                        <td>String</td>\n                        <td>index</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Panel slot\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>名称</th>\n                        <th>说明</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>无</td>\n                        <td>面板头内容</td>\n                    </tr>\n                    <tr>\n                        <td>content</td>\n                        <td>描述内容</td>\n                    </tr>\n                </tbody>\n            </table>\n        </div>\n    </article>\n</div>\n";

