module.exports = "\n\n\n\n\n\n\n\n\n\n\n\n<div>\n    <article>\n        <h1>Modal 对话框</h1>\n        <anchor title=\"概述\" h2></anchor>\n        <p>模态对话框，在浮层中显示，引导用户进行相关操作。</p>\n        <p><code>Modal</code>提供了两种用法，普通组件使用和封装好的简洁实例调用。</p>\n        <anchor title=\"代码示例\" h2></anchor>\n        <anchor title=\"普通组件使用方法\" h3></anchor>\n        <demo title=\"基础用法\">\n            <div slot=\"demo\">\n                <i-button type=\"primary\" on-select=\"handleModal1\">显示对话框</i-button>\n                <Modal\n                    value=\"{{modal1}}\"\n                    width=\"{{360}}\"\n                    title=\"普通的Modal对话框标题\"\n                    on-ok=\"ok\"\n                    on-close=\"closeModal1\">\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                </Modal>\n            </div>\n            <div slot=\"desc\">\n                <p>最简单的使用方法，通过控制属性<code>value</code>来显示 / 隐藏对话框。</p>\n                <p>默认按键盘<code>ESC</code>键也可以关闭。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.base }}</i-code>\n        </demo>\n        <demo title=\"自定义样式\">\n            <div slot=\"demo\">\n                <i-button on-select=\"handleModal2\">自定义页头和页脚</i-button>\n                <Modal \n                    s-if=\"modal2\"\n                    value=\"{{modal2}}\" \n                    on-close=\"closeModal2\"\n                    >\n                    <p slot=\"header\" style=\"color:#f60;text-align:center\">\n                        <Icon type=\"information-circled\"></Icon>\n                        <span>删除确认</span>\n                    </p>\n                    <div style=\"text-align:center\">\n                        <p>此任务删除后，下游 10 个任务将无法执行。</p>\n                        <p>是否继续删除？</p>\n                    </div>\n                    <div slot=\"footer\">\n                        <i-button type=\"error\" size=\"large\" long loading=\"{=modal_loading=}\" on-select=\"del\">删除</i-button>\n                    </div>\n                </Modal>\n                <i-button on-select=\"handleModal3\">不带标题栏</i-button>\n                <Modal \n                    s-if=\"modal3\"\n                    value=\"{=modal3=}\"\n                    showHead=\"{{false}}\"\n                    on-close=\"closeModal3\"\n                    >\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                </Modal>\n                <i-button on-select=\"handleModal5\">设置宽度</i-button>\n                <Modal\n                    s-if=\"modal5\"\n                    value=\"{{modal5}}\"\n                    title=\"自定义宽度\"\n                    width=\"{{300}}\"\n                    on-close=\"closeModal5\">\n                    <p>自定义宽度，单位 px，默认 520px。</p>\n                    <p>对话框的宽度是响应式的，当屏幕尺寸小于 768px 时，宽度会变为自动<code>auto</code>。</p>\n                </Modal>\n            </div>\n            <div slot=\"desc\">\n                <p>Modal 组件提供了灵活的自定义样式 API 和 Slot，可以自由控制整个 Modal 的各个组成部分，比如页头、页脚、关闭按钮。</p>\n                <p>通过和其它组件的交互，能实现更复杂的功能，满足了大多业务场景。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.custom }}</i-code>\n        </demo>\n        <demo title=\"禁用关闭\">\n            <div slot=\"demo\">\n                <i-button on-select=\"handleModal8\">禁用遮罩层关闭</i-button>\n                <Modal\n                    title=\"对话框标题\"\n                    value=\"{{modal8}}\"\n                    showFooter=\"{{false}}\"\n                    maskCloseable=\"{{false}}\"\n                    on-close=\"closeModal8\">\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                </Modal>\n            </div>\n            <div slot=\"desc\">\n                <p>可以禁用关闭和遮罩层关闭。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.close }}</i-code>\n        </demo>\n        <demo title=\"自定义位置\">\n            <div slot=\"demo\">\n                <i-button on-select=\"handleModal9\">距离顶部 20px</i-button>\n                <Modal\n                    title=\"对话框标题\"\n                    value=\"{{modal9}}\"\n                    styles=\"{{styles}}\"\n                    on-close=\"closeModal9\">\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                    <p>对话框内容</p>\n                </Modal>\n            </div>\n            <div slot=\"desc\">\n                <p>可以自定义 Modal 的对话框样式 以及 对话框 Wrap 的 class 名称，从而实现更多自定义的样式，比如垂直居中。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.position }}</i-code>\n        </demo>\n        <div class=\"api\">\n            <anchor title=\"API\" h2></anchor>\n            <anchor title=\"Modal props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>value</td>\n                        <td>对话框是否显示</td>\n                        <td>Boolean</td>\n                        <td>false</td>\n                    </tr>\n                    <tr>\n                        <td>title</td>\n                        <td>对话框标题，如果使用 slot 自定义了页头，则 title 无效</td>\n                        <td>String</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>showHead</td>\n                        <td>是否显示标题栏</td>\n                        <td>Boolead</td>\n                        <td>true</td>\n                    </tr>\n                    <tr>\n                        <td>showFooter</td>\n                        <td>是否显示Footer</td>\n                        <td>Boolead</td>\n                        <td>true</td>\n                    </tr>\n                    <tr>\n                        <td>closable</td>\n                        <td>是否显示右上角的关闭按钮，关闭后 Esc 按键也将关闭</td>\n                        <td>Boolean</td>\n                        <td>true</td>\n                    </tr>\n                    <tr>\n                        <td>maskCloseable</td>\n                        <td>是否允许点击遮罩层关闭</td>\n                        <td>Boolean</td>\n                        <td>true</td>\n                    </tr>\n                    <tr>\n                        <td>okText</td>\n                        <td>确定按钮文字</td>\n                        <td>String</td>\n                        <td>确定</td>\n                    </tr>\n                    <tr>\n                        <td>cancelText</td>\n                        <td>取消按钮文字</td>\n                        <td>String</td>\n                        <td>取消</td>\n                    </tr>\n                    <tr>\n                        <td>width</td>\n                        <td>对话框宽度，对话框的宽度是响应式的，当屏幕尺寸小于 768px 时，宽度会变为自动<code>auto</code>。当其值不大于 100 时以百分比显示，大于 100 时为像素</td>\n                        <td>Number | String</td>\n                        <td>520</td>\n                    </tr>\n                    <tr>\n                        <td>styles</td>\n                        <td>设置浮层样式，调整浮层位置等，该属性设置的是<code>.ivu-modal</code>的样式</td>\n                        <td>Object</td>\n                        <td>-</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Modal events\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>事件名</th>\n                        <th>说明</th>\n                        <th>返回值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>on-ok</td>\n                        <td>点击确定的回调</td>\n                        <td>无</td>\n                    </tr>\n                    <tr>\n                        <td>on-close</td>\n                        <td>点击取消/蒙层/关闭按钮 的回调，必须存在</td>\n                        <td>无</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Modal slot\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>名称</th>\n                        <th>说明</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>header</td>\n                        <td>自定义页头</td>\n                    </tr>\n                    <tr>\n                        <td>footer</td>\n                        <td>自定义页脚内容</td>\n                    </tr>\n                    <tr>\n                        <td>close</td>\n                        <td>自定义右上角关闭内容</td>\n                    </tr>\n                    <tr>\n                        <td>无</td>\n                        <td>对话框主体内容</td>\n                    </tr>\n                </tbody>\n            </table>\n        </div>\n    </article>\n</div>\n";

