module.exports = "\n\n\n\n\n\n<div>\n    <article>\n        <h1>radio 单选框</h1>\n        <anchor title=\"概述\" h2></anchor>\n        <p>基本组件-单选框。主要用于一组可选项单项选择，或者单独用于切换到选中状态。</p>\n        <anchor title=\"代码示例\" h2></anchor>\n        <demo title=\"组合使用\">\n            <div slot=\"demo\">\n                <radio-group label=\"{=phone=}\">\n                    <radio label=\"apple\">\n                        <Icon type=\"social-apple\"></Icon>\n                        <span>Apple</span>\n                    </radio>\n                    <radio label=\"android\">\n                        <Icon type=\"social-android\"></Icon>\n                        <span>Android</span>\n                    </radio>\n                    <radio label=\"windows\">\n                        <Icon type=\"social-windows\"></Icon>\n                        <span>Windows</span>\n                    </radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ phone }}</p>\n                <radio-group label=\"{=animal=}\">\n                    <radio label=\"金斑蝶\"></radio>\n                    <radio label=\"爪哇犀牛\"></radio>\n                    <radio label=\"印度黑羚\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ animal }}</p>\n            </div>\n            <div slot=\"desc\">\n                <p>使用<code>radio-group</code>实现一组互斥的选项组。在组合使用时，<code>radio</code> 使用 <code>label</code> 来自动判断。每个 radio 的内容可以自定义，如不填写则默认使用 label 的值。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.group }}</i-code>\n        </demo>\n        <demo title=\"不可用\">\n            <div slot=\"demo\">\n                <radio-group label=\"{=disabledGroup=}\">\n                    <radio label=\"金斑蝶\" disabled></radio>\n                    <radio label=\"爪哇犀牛\"></radio>\n                    <radio label=\"印度黑羚\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ disabledGroup }}</p>\n            </div>\n            <div slot=\"desc\">\n                <p>通过设置<code>disabled</code>属性来禁用单选框。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.disabled }}</i-code>\n        </demo>\n        <demo title=\"垂直\">\n            <div slot=\"demo\">\n                <radio-group label=\"{=vertical=}\" vertical>\n                    <radio label=\"apple\">\n                        <Icon type=\"social-apple\"></Icon>\n                        <span>Apple</span>\n                    </radio>\n                    <radio label=\"android\">\n                        <Icon type=\"social-android\"></Icon>\n                        <span>Android</span>\n                    </radio>\n                    <radio label=\"windows\">\n                        <Icon type=\"social-windows\"></Icon>\n                        <span>Windows</span>\n                    </radio>\n                </radio-group>\n            </div>\n            <div slot=\"desc\">\n                <p>设置属性 <code>vertical</code> 可以垂直显示，按钮样式下无效。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.vertical }}</i-code>\n        </demo>\n        <demo title=\"按钮样式\">\n            <div slot=\"demo\">\n                <radio-group label=\"{=button1=}\" type=\"button\">\n                    <radio label=\"北京\"></radio>\n                    <radio label=\"上海\"></radio>\n                    <radio label=\"深圳\"></radio>\n                    <radio label=\"杭州\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ button1 }}</p>\n                <radio-group label=\"{=button2=}\" type=\"button\">\n                    <radio label=\"北京\"></radio>\n                    <radio label=\"上海\" disabled></radio>\n                    <radio label=\"深圳\"></radio>\n                    <radio label=\"杭州\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ button2 }}</p>\n                <radio-group label=\"{=button3=}\" type=\"button\">\n                    <radio label=\"北京\" disabled></radio>\n                    <radio label=\"上海\" disabled></radio>\n                    <radio label=\"深圳\" disabled></radio>\n                    <radio label=\"杭州\" disabled></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ button3 }}</p>\n            </div>\n            <div slot=\"desc\">\n                <p>组合使用时可以设置属性<code>type</code>为 button 来应用按钮的样式。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.button }}</i-code>\n        </demo>\n        <demo title=\"尺寸\">\n            <div slot=\"demo\">\n                <radio-group label=\"{=button4=}\" type=\"button\" size=\"large\">\n                    <radio label=\"北京\"></radio>\n                    <radio label=\"上海\"></radio>\n                    <radio label=\"深圳\"></radio>\n                    <radio label=\"杭州\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ button4 }}</p>\n                <radio-group label=\"{=button5=}\" type=\"button\">\n                    <radio label=\"北京\"></radio>\n                    <radio label=\"上海\"></radio>\n                    <radio label=\"深圳\"></radio>\n                    <radio label=\"杭州\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ button5 }}</p>\n                <radio-group label=\"{=button6=}\" type=\"button\" size=\"small\">\n                    <radio label=\"北京\"></radio>\n                    <radio label=\"上海\"></radio>\n                    <radio label=\"深圳\"></radio>\n                    <radio label=\"杭州\"></radio>\n                </radio-group>\n                <p class=\"demo-data\">{{ button6 }}</p>\n            </div>\n            <div slot=\"desc\">\n                <p>通过设置属性<code>size</code>为<code>large</code>或<code>small</code>将按钮样式设置为大和小尺寸，不设置为默认(中)尺寸。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.size }}</i-code>\n        </demo>\n        <div class=\"api\">\n            <anchor title=\"API\" h2></anchor>\n            <anchor title=\"Radio props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>label </td>\n                        <td>只在组合使用时有效。指定当前选项的 value 值</td>\n                        <td>String | Number</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>disabled</td>\n                        <td>是否禁用当前项</td>\n                        <td>Boolean</td>\n                        <td>false</td>\n                    </tr>\n                    <tr>\n                        <td>size</td>\n                        <td>单选框的尺寸，可选值为 <code>large</code>、<code>small</code>、<code>default</code> 或者不设置</td>\n                        <td>String</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>true-value</td>\n                        <td>选中时的值，当使用类似 1 和 0 来判断是否选中时会很有用</td>\n                        <td>String, Number, Boolean</td>\n                        <td>true</td>\n                    </tr>\n                    <tr>\n                        <td>false-value</td>\n                        <td>没有选中时的值，当使用类似 1 和 0 来判断是否选中时会很有用</td>\n                        <td>String, Number, Boolean</td>\n                        <td>false</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Radio Group props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>label</td>\n                        <td>指定当前选中的项目数据。可以双向绑定数据</td>\n                        <td>String | Number</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>type</td>\n                        <td>可选值为 button 或不填，为 button 时使用按钮样式</td>\n                        <td>String</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>size</td>\n                        <td>尺寸，可选值为<code>large</code>、<code>small</code>、<code>default</code>或者不设置</td>\n                        <td>String</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>vertical</td>\n                        <td>是否垂直排列，按钮样式下无效</td>\n                        <td>Boolean</td>\n                        <td>false</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Radio Group events\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>事件名</th>\n                        <th>说明</th>\n                        <th>返回值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>on-select</td>\n                        <td>在选项状态发生改变时触发，返回当前选中的项。通过修改外部的数据改变时不会触发</td>\n                        <td>...</td>\n                    </tr>\n                </tbody>\n            </table>\n        </div>\n    </article>\n</div>\n";

